import lInput from '../../components/login-comp/login-input.vue'; //input
import lButton from '../../components/login-comp/login-button.vue'; //button
export default {
	data() {
		return {
			phoneData: '', //用户/电话
			passData: '', //密码
			isRotate: false, //是否加载旋转
			logoImage: ''
		};
	},
	components: {
		lInput,
		lButton
	},
	methods: {
		startLogin() {
			//登录
			if (this.isRotate) {
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.phoneData.length == '') {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '用户名不能为空'
				});
				return;
			}
			if (this.passData.length == '') {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '密码不能为空'
				});
				return;
			} else if (this.passData.length < 6) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '密码最少为6位'
				});
				return;
			}

			console.log('登录成功');

			this.isRotate = true;
			setTimeout(() => {
				uni.showToast({
					title:'登录成功',
					icon: 'success',
					position: 'bottom',
					success: () => {
						uni.switchTab({
							url:'../tabbar/task/task',
							complete: () => {
								this.isRotate = false;
							}
						})
					}
				});
				
			}, 1000);
		},
		login_weixin() {
			//微信登录
			uni.showToast({
				icon: 'none',
				position: 'bottom',
				title: '微信登录'
			});
		}
	}
};
